<template>
  <div class="HospitalZoneView">
    <h1>请确认就诊院区，选择对应的院区就诊！</h1>
    <div v-for="(item, index) in HospitalZoneList" :key="index">
      <el-row justify="center">
        <el-card
          :header="item.aliasName"
          shadow="always"
          @click="ToDeptList(item)"
        >
          <el-image :src="item.hosPhoto" class="HospitalZoneImage"></el-image>
        </el-card>
      </el-row>
    </div>
  </div>
</template>

<script>
import { API, Config } from "@/utils/API";
import { Store } from "@/stores";
import { getPhsSign } from "@/utils/Func";
import { ElMessage } from "element-plus";

const store = Store();
export default {
  name: "HospitalZone",
  data() {
    return {
      HospitalZoneList: [],
    };
  },
  methods: {
    get_hospital: function () {
      const headers = {
        phsSign: getPhsSign(),
        phsId: Config.phsid,
        "Content-Type": "application/json",
      };
      console.log(headers);
      const data = { args: { sysCode: Config.syscode }, token: store.token };
      this.axios
        .post(API.get_hospital, data, { headers: headers })
        .then((res) => {
          if (res.data.code === "0") {
            this.HospitalZoneList = res.data.result;
          } else {
            ElMessage.error(res.data.message || "获取医院信息失败");
          }
        });
    },
    ToDeptList: function (item) {
      store.HospitalZone = item;
      this.$router.push("/deptlist");
    },
  },
  mounted() {
    if (!store.token) {
      this.$router.push("/login");
    } else {
      this.get_hospital();
    }
  },
};
</script>

<style scoped>
.HospitalZoneImage {
  max-width: 400px;
  max-height: 200px;
}

.HospitalZoneView {
  text-align: center;
  align-items: center;
}
</style>
